.app__archives{
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: auto;
    margin-top: 1rem;
    /* background-color: blueviolet; */

}

.app_archives-header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    /* background-color: aqua; */
    height: 200px;
}

.app__archives-banner {
    width: 100%;
    /* margin: auto; */
    /* display: flex; */
    /* flex-direction: column; */
    height: auto;
    /* background-color: aquamarine; */
    /* justify-content: center; */
    align-items: center;
    position: relative;
  overflow: hidden;
  white-space: nowrap;
   
}

.moving-image {
    display: inline-block;
    animation: slideImages 10s linear infinite; /* Adjust animation duration and properties */
  }



  @keyframes slideImages {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-100%); /* Adjust the percentage based on the width of the images */
    }
  }


.app__archives-banner img{
/* height: 100%; */
width: 100%;
/* object-fit: cover; */
}

.app_archives-text{
/* height: 200px; */
/* background-color: beige; */
display: flex;
flex-direction: column;
justify-content: center;
/* align-items: fl; */
}

.app_archives-img{
    width: 30%;
    /* background-color: aquamarine; */
    display: flex;
    justify-content: flex-end;
}

.app_archives-img img{
    /* width: 40%; */
    height: 100%;
    
}



.app__archives h1{
    /* margin-top: 2rem; */
}



.app__archives-box-holder{
    margin-top: 2rem;
    /* background-color: aquamarine; */
}

@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__archives{

        align-items: center;
        justify-content: center;
        text-align: center;
        margin-top: 2rem;
    }
    



    .app__archivebox-head{

        align-items: center;
        justify-content: center;
        text-align: center;
        /* background-color: blue; */
    }

    .app__archivebox-paragraph {

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }

    .app__archivebox-tagline{

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }


}

@media screen and (max-width: 650px) {
    .app_archives-header{
        flex-direction: column;
        height: 600px;
        /* background-color: aqua; */
    }  
    
    .app_archives-img{
        width: 100%;
        /* background-color: aquamarine; */
        display: flex;
        justify-content: center;
    }
    
    .app_archives-img img{
        /* width: 40%; */
        height: 50%;
        
    }

}

