
.app__archivebox {
    width: 100%;
    margin: auto;
    
    display: flex;
    flex-direction: row;
    /* height: 350px; */
    /* background-color: aquamarine; */
    padding-bottom: 2rem;
    justify-content: flex-start;
    margin: 1rem;
}

.app__archivebox-img{
    width: 30%;
    /* height: 100%; */
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    /* background-color: blue; */
    

}

.app__archivebox-img img{
    width: 100%;
    /* height: 90%; */
    /* object-fit: contain; */

}

.app__archivebox-head-img{
    width: 100%;
}

.app__archivebox-head-img img{
width: 100%;
}

.app__archivebox-content{
    width: 70%;
    padding: 1rem;
    padding-top:0rem;
    /* background-color: blueviolet; */
}

.app__archivebox-tagline p{
    color: var(--color-grey);
}

.app__archivebox-action{
 padding-top: 1rem;
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 font-style: italic;
}

.app__archivebox__icon{
    /* padding: 1rem; */
    color:var(--color-black);
    font-size: 24px;
    padding: 0.5rem ;
    padding-left: 1.5rem;
    padding-top: 0.6rem;
    /* background-color: aqua; */

}

.app__archivebox-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__archivebox-tagline{
    color: var(--color-grey);
}


.app__archivebox-paragraph{
    padding: 1rem;
    /* background-color: aquamarine; */
}


@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__archivebox{
        width: 90%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
        /* background-color: aqua; */
        margin-top: 2rem;
        
        
    }

    .app__archivebox-action{
        padding-top: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
       }

    .app__archivebox-img{
        width: 90%;
    }

    .app__archivebox-content{

        align-items: center;
        justify-content: center;
        /* background-color: blueviolet; */
    }

    .app__archivebox-head-img{

        align-items: center;
        justify-content: center;
        text-align: center;
        /* background-color: blue; */
    }

    .app__archivebox-content{
        width:100%;
    }
    .app__archivebox-paragraph {

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;
        /* width: 100%; */

    }

    .app__archivebox-tagline{

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }


}

