.radios-form{
    width: 90%;
    height: auto;
    margin: auto;
    /* background-color: aqua; */
    
    /* padding: 1rem; */
}

.radios-form__add{
    width: 100%;
    height: auto;
    margin: auto;
    padding: 1rem;
    /* background-color: antiquewhite; */
    border-bottom: 1px solid var(--color-belgravia);
}




.radios-form__show{
    width: 100%;
    height: auto;
    margin:  auto;
    /* padding:1rem 0; */
    /* background-color: aquamarine; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: 1px solid var(--color-belgravia);
    /* padding: 1rem; */
    

}

