.app__subpageheaderimage{
    width: 100%;
    /* margin: auto; */
    display: flex;
    flex-direction: column;
    height: 220px;
    background-color: #C4C5C0;
    /* justify-content: center; */
    align-items: center;
    margin-top: 2rem;
    /* background-image: url('../../assets/attire/banner.jpg'); */
    /* background-size: contain; */
    /* background-position: center; */
}


.app__subpageheaderimage img{
 height: 100%;   
}



.app__subpageheader-content{

    padding: 1rem;
}

.app__subpageheader-action{
 padding-top: 1rem;
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 font-style: italic;
}

.app__subpageheader-icon{
    /* padding: 1rem; */
    color:var(--color-black);
    font-size: 24px;
    padding: 0.5rem ;
    padding-left: 1.5rem;
    padding-top: 0.6rem;
    /* background-color: aqua; */

}

.app__subpageheader-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.app__subpageheaderimage img{
 width: 100%;  
 object-fit: contain; 
}



@media screen and (max-width: 1150px) {
 
.app__subpageheaderimage{
    background-color: #fff;
    height: 180px;
    /* margin-bottom: 0; */

    }

.app__subpageheaderimage img{
    width: 100%;   
    /* object-fit: contain;  */
   }
}