
.app__map{

    width: 90%;
    margin: auto;
    margin-top: 2rem;
    padding-bottom: 2rem;
    
    display: grid;
    grid-template-columns: auto auto;
    /* grid-column-start: auto; */
    
    
    justify-content: center;
    align-items: center;
    /* background-color: aqua; */
}

.app__map-box{
    /* background-color: bisque; */
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 2rem;
}

.app__map-header{
    width: 90%;
margin: auto;
/* background-color: aqua; */
height: auto;
display: flex;
justify-content: center;
margin-top: 2rem;
}

.app__map-img{
    /* background-color: antiquewhite; */
    height: auto;
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: center;

}
.app__map-img img{
 width: 90%;   
}

.app__map-front{
    width: 90%;
    display: flex;
    margin-top: 4rem;
    /* background-color: aqua; */
    margin: auto;
    
}

.app__map-img{
    width: 90%;
    /* background-color: aqua; */
}
.app__map-text{
    text-align: center;
    margin-top: 6rem;
}












@media screen and (max-width: 1150px) {

    .app__map-front{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        /* background-color: aqua; */
        margin: auto;
    }

    .app__map-text{
        width: 80%;

        
    }

    .app__map-img{
        width: 90%;
        margin-top: 2rem;
        /* background-color: aqua; */
    }
    
}

@media screen and (max-width: 450px) {
    .app__chef-sign img {
        width: 80%;
    }
}