.section__padding {
  padding: 4rem 6rem;
}

.flex__center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex__tabs{
  display: flex;
  align-items: center;
  /* justify-content: center; */
}

.active-tab{
  border-right: 3px solid #007bff;
}

.icon-inline{
  margin-right: 1rem;
}

.app__bg {
  /* background: url('./assets/bg.png'); */
  background-position: center;
  background-size: cover;
  background-repeat: repeat;
  background-attachment: fixed;
}

.app__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  min-height: 95vh;
}

.app__wrapper_info {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.app__wrapper_img {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  margin-left: 2rem;
}



.app__wrapper_img-reverse {
  justify-content: flex-start;
  margin-right: 2rem;
}

.app__wrapper_img img {
  width: 80%;
}

.custom__button {
  background-color: var(--color-orange);
    color: var(--color-white);
    font-family: var(--font-base);
    font-weight: 700;
    letter-spacing: 0.04em;
    line-height: 28px;
    font-size: 16px;
    padding: 0.5rem 1.5rem;
    border-radius: 1px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 8px;
}

.p__cormorant {
  font-family: var(--font-base);
  color: var(--color-black);
  font-weight: 700;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-feature-settings: 'tnum' on, 'lnum' on;
  line-height: 29.9px;
  font-size: 15px;
  /* background-color: antiquewhite; */
  
}

.p__cormorant-small {
  font-family: var(--font-base);
  color: var(--color-black);
  font-weight: 700;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-feature-settings: 'tnum' on, 'lnum' on;
  /* line-height: 29.9px; */
  font-size: 10px;
  /* background-color: #007bff; */
}



.font_style_italic{
  font-style: italic;
}

.p__opensans {  
  font-family: var(--font-alt);
  color: var(--color-dark-gray);
  font-weight: 400;
  letter-spacing: 0.04em;
  /* text-transform: capitalize; */
  line-height: 28px;
  font-size: 14px;
}

.headtext__cormorant {
  font-family: var(--font-base);
  color: var(--color-black);
  font-size: 40px;
  /* line-height: 55px; */
  letter-spacing: 0.04em;
  text-transform: capitalize;
  margin-bottom: 1rem;
  /* background-color: aqua; */
  
}

.headtext__cormorant2 {
  font-family: var(--font-base);
  color: var(--color-black);
  font-size: 35px;
  /* line-height: 55px; */
  letter-spacing: 0.04em;
  /* text-transform: capitalize; */
  margin-bottom: 1rem;
  /* background-color: aqua; */
}

.subheadtext__cormorant {
  font-family: var(--font-base);
  color: var(--color-black);
  font-size: 30px;
  /* line-height: 55px; */
  letter-spacing: 0.04em;
  text-transform: capitalize;
  /* margin-bottom: 1rem; */
  /* background-color: aquamarine; */
  font-style: italic;
}


.subheading__cormorant {
  font-family: var(--font-base);
  color: var(--color-black);
  font-size: 20px;
  /* line-height: 55px; */
  letter-spacing: 0.04em;
  text-transform: capitalize;
  margin-left: 1rem;
  /* margin-bottom: 1rem; */
  /* background-color: aquamarine; */
  /* font-style: italic; */
}



.spoon__img {
  width: 45px;
}

@media screen and (min-width: 2000px) {
  .custom__button,
  .p__cormorant {
    font-size: 37px;
    line-height: 67px;
  }

  .p__opensans {
    font-size: 30px;
    line-height: 50px;
  }

  .headtext__cormorant {
    font-size: 150px;
    line-height: 210px;
  }

  .spoon__img {
    width: 80px;
  }
}

@media screen and (max-width: 1150px) {
  .app__wrapper {
    flex-direction: column;
  }

  .app__wrapper_img {
    margin: 5rem 0 0 0;
  }

  .app__wrapper_img-reverse {
    margin: 0 0 5rem 0;
  }

  .app__wrapper_img img {
    width: 100%;
  }

  .headtext__cormorant {
    font-size: 35px;
    line-height: 70px;
  }

  .headtext__cormorant2 {
    font-size: 30px;
   
  }

  
.active-tab{
  border-bottom: 3px solid #007bff;
  border-right:0;
}

.div-border-bottom{
  border-bottom: 1px solid #888;
}


}

@media screen and (max-width: 850px) {
  .section__padding {
    padding: 4rem;
  }
}

@media screen and (max-width: 650px) {
  .section__padding {
    padding: 4rem 2rem;
  }
  
  .p__cormorant {
    font-size: 19px;
  }

  .p__opensans {
    font-size: 14px;
  }
}

@media screen and (max-width: 450px) {
  .p__opensans {
    font-size: 12px;
  }

  .p__cormorant {
    font-size: 19px;
  }

  .headtext__cormorant {
    font-size: 45px;
    line-height: 70px;
  }
}
