.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background:  var(--color-white); */
    background-image: url('../../assets/nav-back.png');
    padding: 1rem 2rem;
    /* background-color: aqua; */
}

.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    /* background-color: antiquewhite; */
}

.app__navbar-logo img {
    width: 250px;
}

.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}

.app__navbar-links li {
    margin: 0 1.2rem;
    cursor: pointer;
    font-style: italic;
}

.app__navbar-links li:hover {
    color: var(--color-gray);
}

.app__navbar-login {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.app__navbar-login a {
    margin: 0 1rem;
    text-decoration: none;
    transition: .5s ease;
}

.app__navbar-login a:hover {
    border-bottom: 1px solid var(--color-golden);
}

.app__navbar-login div {
    width: 1px;
    height: 30px;
    background: var(--color-grey);
}

.app__navbar-smallscreen {
    display: none;
}

.app__navbar-smallscreen-logo{
    /* width: 50%; */

    /* background-color: aqua; */
    /* margin: auto; */
}

.nav-search-icon{
    width: 90px;
    /* padding-le: 40px; */
    /* background-color: aqua; */
}

.nav-search-icon img{
 width: 80%;
 margin-left: 20%;
}



.app__navbar-smallscreen_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    /* padding: 5vh; */
    max-height: 100%;
    overflow: auto;
    background: var(--color-cream);
    transition: .5s ease;
     
    flex-direction: column;
    z-index: 5;
    /* background-color: aqua; */
}

.app__navbar-smallscreen_overlay .overlay__close {
    font-size: 20px;
    color: var(--color-golden);
    cursor: pointer;

    position: absolute;
    top: 20px;
    right: 20px;
}

.app__navbar-smallscreen_links {
    list-style: none;
}

.app__navbar-smallscreen_links li {
    margin: 1rem;
    cursor: pointer;
    color: var(--color-golden);
    font-size: 1rem;
    text-align: center;
    font-family: var(--font-base);
}

.app__navbar-smallscreen_links li:hover {
    color: var(--color-white);
}

@media screen and (min-width: 2000px) {
    .app__navbar-logo img {
        width: 210px;
    }
}

@media screen and (max-width: 1150px) {
    .app__navbar-links {
        display: none;
    }

    .app__navbar-smallscreen {
        display: flex;
        /* justify-content: center;
        align-items: center; */
        /* background-color: aquamarine; */
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: fit-content;
    }

    .app__navbar-smallscreen-logo{
            width: 50%;
            /* background-color: aqua; */
            display: flex;
            justify-content: center;
            
            
    }

    .app__navbar-smallscreen-logo img{
        width: 100%;
        /* margin: auto; */
        /* background-color: aquamarine; */
    }

}

