.app__archivesdetails{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    /* background-color: aquamarine; */
    /* margin-top: 1rem; */
    width: 90%;
    margin: auto;
    
}


.app__archivesdetails-header{
    width: 90%;
    height: 300px;
    /* background-color: aqua; */
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.app__archivesdetails-header img{
    padding: 1rem;
    width: 60%; 
}

.app__archivesdetails-paragraph{
    width: 60%;
    text-align: center;
}

.app__archivesdetails-imgholder{
    width: 100%;
    /* background-color: blue; */
    /* padding: 1rem; */
    display: grid;
    grid-template-columns: auto auto auto;
    /* flex-direction: row; */
    justify-content: space-around;
    /* width: 30%; */
    /* background-color: aqua; */
}

.app__archivesdetails-videoholder{
    width: 100%;
    /* height: 90; */
    margin: auto;
    /* background-color: blue; */
    grid-column: 1/ span 3;
    /* padding: 1rem; */
    margin-top: 5rem;
    /* justify-self: end; */

    /* grid-template-columns: auto; */
    /* flex-direction: row; */
    /* justify-content: space-around; */
    /* width: 30%; */

}


.app__archivesdetails-img{
    /* background-color: blueviolet; */
    margin: 1rem;
    display: flex;
    align-items: center;

    /* width: 40%; */
}

.app__archivesdetails-img img{
    width: 100%;
    /* margin: auto; */
    

}




@media screen and (max-width: 850px) {

    .app__archivesdetails-imgholder{
        grid-template-columns: auto;
    }

    .app__archivesdetails-img img{
        width: 70%;
        margin: auto;
    }

}