
.app__eventbox {
    width: 100%;
    margin: 0;
    margin-top: 2rem;
    display: flex;
    flex-direction: row;
    /* height: 350px; */
    /* background-color: aquamarine; */
    align-items: flex-start;
}

.app__eventbox-img{
    width: 40%;
    /* height: 100%; */
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    

}

.app__eventbox-img img{
    width: 100%;
    /* height: 90%; */
    /* object-fit: contain; */

}

.app__eventbox-content{
    width: 60%;
    /* padding: 1rem; */
    padding-left: 2rem;
    /* background-color: blueviolet; */
}

.app__eventbox-tagline p{
    color: var(--color-grey);
}

.app__eventbox-action{
 padding-top: 1rem;
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 font-style: italic;
}

.app__eventbox__icon{
    /* padding: 1rem; */
    color:var(--color-black);
    font-size: 24px;
    padding: 0.5rem ;
    padding-left: 1.5rem;
    padding-top: 0.6rem;
    /* background-color: aqua; */

}

.app__eventbox-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__eventbox-tagline{
    color: var(--color-grey);
}


.app__eventbox-paragraph{
    padding-top: 1rem;
}


@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__eventbox{
        width: 90%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
        /* background-color: aqua; */
        margin-top: 2rem;
        /* background-color: aqua; */
        /* align-items: center; */
        
        
        
    }

    .app__eventbox-content{
    width: 100%;
    padding-left: 0;
    }

    .app__eventbox-action{
        padding-top: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
       }

    .app__eventbox-img{
        width: 100%;
    }

    .app__eventbox-content{
        width: 80%;
        align-items: center;
        justify-content: center;
        /* background-color: blueviolet; */
    }

    .app__eventbox-head{

        align-items: center;
        justify-content: center;
        /* background-color: blue; */
        text-align: center;
    }

    .app__eventbox-paragraph {

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }

    .app__eventbox-tagline{

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }


}

