.app__speaks{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: auto;
    justify-content: center;
    align-items: center;
    /* background-color: aqua; */
}

.app__speaks-header{
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* height: 250px; */
    align-items: center;

}

.app__speaks-header-img{
    width:40%;
}

.app__speaks-header-img img{
    width:100%;
}

.app__speaks-header-content{
    width: 50%;
    height: 100%;
    /* background-color: aqua; */
}

.app__speaks-header-paragraph{
    text-align: center;
}

.app__speaks-header-tagline{
    text-align: center;
    margin-top: 1rem;
}


@media screen and (min-width: 2000px) {
    .app__chef-sign img {
        width: 370px;
    }
}

@media screen and (max-width: 450px) {
    .app__chef-sign img {
        width: 80%;
    }
}

.app__speaks-content{
    /* background-color: antiquewhite; */
}



@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__speaks-header{
        flex-direction: column;
        /* background-color: aqua; */
        margin: 2rem;
    }

    .app__speaks-header-img{
        width:60%;
    }

    .app__speaks-header-content{
        width: 80%;
        height: 100%;
        /* background-color: aquamarine; */
        margin-top: 1rem;
    }




}

