.app__header{
    background-color: #CFCDBF;
    /* background-image: url('https://storage.googleapis.com/thecockneysikh-storage/homepage/homepage3.png'); */
    /* background-color: #111; */
    /* background-image: url("../../assets/header2.jpg"); */
    
                     
    background-repeat:   no-repeat;
    background-size: cover;
    
    /* background-position: center center; */
    /* height: 900px; */
    width: 100%;
    height: 100vh;
    /* height: auto; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__header img{
    width: 100%;
    object-fit: fill;
}

.app__header-container{
    width: 100%;
    height: 100%;
    /* background-color:aqua; */
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: start;
        
}

.app__header-container img{
    width: 100%;
    /* height: 100%; */
    object-fit:cover;

}

.app__header-img{
    width: 80%;
    height: 90%;
    background-color: var(--color-belgravia);
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding-top: 4rem; */
    /* background-color:aqua; */
    
    
}

.app__header-img img{
    height: 100%;
    /* width: 100%; */
    object-fit: fill;
    
}

.app__header-h1 {
    font-family: var(--font-base);
    color: var(--color-golden);
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 117px;
    font-size: 90px;
}

/* .app__wrapper_img{
    width: 100%;
    background-color: aqua;
} */

/* .app__header img {
    width: 100%;
} */

@media screen and (min-width: 2000px) {
    .app__header-h1 {
        font-size: 180px;
        line-height: 210px;
    }
}

@media screen and (max-width: 1150px) {
    .app__header{
        background-position: 50% 50%;
    }

    .app__header-h1 {
        font-size: 90px;
    }

    .app__header-container{
        width:100%;
        /* height: 100vh; */
        /* background-color: antiquewhite; */
    }

    .app__header-container img{
        height: 100%;
        object-fit: contain;
    }

    /* .app__header{
        background-image: url("../../assets/mobile-header.jpg");
    } */

}

@media screen and (max-width: 850px) {
    
    .app__header{
        /* background-image: url("../../assets/mobile-header.jpg"); */
    }

    .app__header-img{
        width: 90%;
    }

    .app__header-img img{
        /* width: 90%; */
        object-fit: contain;
    }

    .app__header-h1 {
        font-size: 70px;
        line-height: 87px;
    }


}

@media screen and (max-width: 450px) {
    .app__header-h1 {
        font-size: 50px;
        line-height: 70px;
    }

    .app__header-img{
        /* background-color: var(--color-belgravia-light); */
    }
}