
.app__formdisplayitem {
    width: 40%;
    margin: auto;
    
    display: flex;
    flex-direction: column;
    height: auto;
    /* background-color: coral; */
    justify-content:flex-start;
    align-items: flex-start;
    margin: 1rem;
    background-color: #ffffff; /* Background color for the div */
    border: 2px solid #e5e5e5; /* Light gray border with 2px width */
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1); 
    text-align: center;
    /* padding: 1rem; */
}

.app__formdisplayitem.expanded {
    /* Set your expanded width and height */
    width: 100%; /* Change to your desired expanded width */
    height: fit-content; /* Change to your desired expanded height */
    /* Other styles */
}

.app__formdisplayitem-img.expanded img{
    width: 50%;
    height: 50%;
    object-fit: cover;
    

}




.app__formdisplayitem-img{
    width: 100%;
    height: 40%;
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 100%; */
    

}

.app__formdisplayitem-video{
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__formdisplayitem-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    

}

.app__formdisplayitem-content{
    width: 100%;
    /* margin: 0 auto; */
    /* background-color: antiquewhite; */
    /* padding: 1rem; */
}

.app__formdisplayitem-action{
 padding: 1rem;
 display: flex;
 flex-direction: row;
 justify-content: center;
 align-items: center;
 font-style: italic;
 /* background-color:chartreuse; */
}


.app__formdisplayitem__icon{
    /* padding: 1rem; */
    color:var(--color-black);
    font-size: 24px;
    padding: 0.5rem ;
    padding-left: 1.5rem;
    padding-top: 0.6rem;
    /* background-color: aqua; */

}

.app__formdisplayitem-head {
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: blueviolet; */
}

.app__formdisplayitem-paragraph{
    
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: red; */
    text-align: center;
    margin: auto;

}



@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__formdisplayitem{
        width: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
        /* background-color: aqua; */
        margin-top: 4rem;
        
        
    }

    .app__formdisplayitem-content{
        width: 100%;
    
        /* padding: 1rem; */
    }

    .app__formdisplayitem-action{
        padding-top: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
       }

    .app__formdisplayitem-img{
        width: 100%;
    }

    .app__formdisplayitem-content{

        align-items: center;
        justify-content: center;
        /* background-color: blueviolet; */
    }

    .app__formdisplayitem-head{

        align-items: center;
        justify-content: center;
        text-align: center;
        /* background-color: blue; */
    }

    .app__formdisplayitem-paragraph{
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }



 

}

