
.app__speaksdetails {
    width: 90%;
    margin: auto;
    margin-top: 2rem;
    display: flex;
    flex-direction: row;
    /* height: 350px; */
    /* background-color: aquamarine; */
}

.app__speaksdetails-img{
    width: 40%;
    /* height: 100%; */
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 100%; */
    

}

.app__speaksdetails-spotify{
    width: 100%;
    height: auto;
    padding-top: 1rem;
}

.app__speaksdetails-img img{
    width: 100%;
    /* height: 90%; */
    /* object-fit: contain; */

}

.app__speaksdetails-content{
    width: 50%;

    padding: 1rem;
    padding-left: 2rem;
    /* background-color: blueviolet; */
}

.app__speaksdetails-tagline p{
    color: var(--color-grey);
    /* margin-bottom: 2rem; */
}

.app__speaksdetails-action{
 padding-top: 1rem;
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 font-style: italic;
 /* background-color: aqua; */

}

.app__speaksdetails-action a{
    margin-right: 1rem;
}

.app__speaksdetails__icon{
    /* padding: 1rem; */
    color:var(--color-black);
    font-size: 24px;
    padding: 0.5rem ;
    padding-left: 1.5rem;
    padding-top: 0.6rem;
    /* background-color: aqua; */

}

.app__speaksdetails-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__speaksdetails-tagline{
    color: var(--color-grey);
}


.app__speaksdetails-paragraph{
    padding-top: 1rem;
}


@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__speaksdetails{
        width: 90%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
        /* background-color: aqua; */
        margin-top: 2rem;
        
        
    }

    .app__speaksdetails-action{
        padding-top: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
       }

    .app__speaksdetails-img{
        width: 100%;
    }

    .app__speaksdetails-content{
        width: 80%;
        align-items: center;
        justify-content: center;
        /* background-color: blueviolet; */
    }

    .app__speaksdetails-head{

        align-items: center;
        justify-content: center;
        /* background-color: blue; */
        text-align: center;
    }

    .app__speaksdetails-paragraph {

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }

    .app__speaksdetails-tagline{

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }


}

