
.app__gallery {
    width: 90%;
    margin: auto;
    margin-top: 2rem;
    
    display: grid;
    grid-template-columns: auto auto auto;
    flex-direction: row;
    justify-content: space-around;
    /* height: 350px; */
    /* background-color: aquamarine; */
}

.app__gallery-img{
    width: 300px;
    height: 300px;
    /* height: 100%; */
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem;
    overflow: hidden;
    /* height: 100%; */
    

}

.app__gallery-img img{
    height: 100%;
    /* max-width: 100%; */
    object-fit: contain;
    /* height: 100%; */
    /* height: 90%; */
    /* object-fit: contain; */

}

