@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__gallery{
        width: 90%;
        grid-template-columns: auto auto;
        /* flex-direction: column;
        justify-content: center;
        align-items: center; */
        height: auto;
        /* background-color: aqua; */
        margin-top: 2rem;
        
        
    }

    .app__gallery-action{
        padding-top: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
       }

    .app__gallery-img{
        width: 100%;
    }

    .app__gallery-content{

        align-items: center;
        justify-content: center;
        width: 80%;
        padding-left: 0;
        /* background-color: blueviolet; */
    }

    .app__gallery-head{

        align-items: center;
        justify-content: center;
        text-align: center;
        /* background-color: blue; */
    }

    .app__gallery-paragraph {

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }

    .app__gallery-paragraph iframe{
        margin-top: 2rem;
    }

    .app__gallery-tagline{

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }


}

@media screen and (max-width: 850px) {

    .app__gallery{
        width: 90%;
        grid-template-columns: auto;
 
        
        
    }

    

}

