.app__formshomepage{
    width: 90%;
    height: auto;
    display: flex;
    /* margin: auto; */
    margin:5rem auto;

}

.app__formshomepage-tablist{
    width: 30%;
    height: auto;
    /* background-color: var(--color-belgravia-light); */
    border-right: 1px solid var(--color-belgravia);
    
} 

.app__formshomepage-tab{
    /* margin: 1rem; */
    width: 80%;
    margin: 2rem auto;
    
    /* background-color: aquamarine; */
}

.app__formshomepage-content{
    width: 70%;
    height: auto;
    /* background-color: var(--color-belgravia-light); */
}

.back_effect{
    padding: 0.5rem;
    /* background-color: bisque; */

    background-image: linear-gradient(#cce5ff,#e6f2ff);
  background-size: 0 100%;
  background-repeat: no-repeat;
  transition: .4s;
}

.back_effect:hover{
    background-size: 100% 100%;
}



/* Add a media query for screens below 1150px */
@media (max-width: 1150px) {

  .app__formshomepage{
    width: 90%;
    height: auto;
    display: flex;
    flex-direction: column;
    /* margin:5rem auto; */

}



  .app__formshomepage-tablist {
    display: flex; /* Change to a flex container for horizontal layout */
    flex-direction: row; /* Horizontal layout */
    justify-content: flex-start; /* Start from the left */
    align-items: center; /* Center vertically */
    width: 100%; /* Full width */
    overflow-x: auto; /* Add horizontal scrolling */
    border-bottom: 1px solid var(--color-belgravia); 
    border-right: 0;
    /* padding: 1rem 0;  */
    /* background-color: aqua; */
  }

  .app__formshomepage-tab {
    margin: 0 1rem; /* Adjust margin for spacing */
  }

  .app__formshomepage-content {
    width: 100%; /* Full width */
    overflow-x: auto; /* Add horizontal scrolling */
    /* background-color: aquamarine; */
  }
}





