.app__book{
    width: 90%;
    margin: auto;   
    margin-bottom: 10rem; 
}

.app__book-paragraph{
display: block;
/* font-style: italic; */
text-align: center;


}




.app__book-header{
width: 100%;
margin: auto;
/* background-color: aqua; */
height: auto;
display: flex;
justify-content: center;
margin-top: 2rem;
align-items: center;

}

.app__book-img{
    /* background-color: antiquewhite; */
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    

}
.app__book-img img{
 width: 90%;  
 margin-top: 1rem; 
}

.app__book-button{
    margin: 2rem;
    display: flex;
    justify-content: center;
    /* background-color: aqua; */
}










@media screen and (min-width: 2000px) {
    .app__chef-sign img {
        width: 370px;
    }
}

@media screen and (max-width: 450px) {
    .app__chef-sign img {
        width: 80%;
    }
}