.app__capturesdetails{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    /* background-color: aqua; */
    margin-top: 1rem;
    text-align: center;
    
}

.app__capturesdetails-img{
    padding: 1rem;
    width: 50%;
}

.app__capturesdetails-img img{
    width: 100%;
}

.app__article{
    display: flex;
    flex-direction: column;
    width: 90%;

}

.app_article-head{
    display: flex;
    flex-direction: row;
}

.app__article img{
    width: 100%;
    margin-top: 2rem;
}

.app__article-img{



}


.app__article-content{
        margin: 4rem;
}



