
.app__videobox {
    width: 90%;
    margin: auto;
    margin-top: 2rem;
    
    display: flex;
    flex-direction: row;
    /* height: 350px; */
    /* background-color: aquamarine; */
}

.app__videobox-img{
    width: 40%;
    /* height: 100%; */
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    

}

.app__videobox-img img{
    width: 100%;
    height: 100%;
    /* height: 90%; */
    /* object-fit: contain; */

}

.app__videobox-content{
    width: 50%;
    padding: 1rem;
    padding-left: 2rem;
    /* background-color: blueviolet; */
}

.app__videobox-tagline p{
    color: var(--color-grey);
}

.app__videobox-action{
 padding-top: 1rem;
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 font-style: italic;
}

.app__videobox__icon{
    /* padding: 1rem; */
    color:var(--color-black);
    font-size: 24px;
    padding: 0.5rem ;
    padding-left: 1.5rem;
    padding-top: 0.6rem;
    /* background-color: aqua; */

}

.app__videobox-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__videobox-tagline{
    color: var(--color-grey);
}


.app__videobox-paragraph{
    padding-top: 1rem;
}


.app__videobox-paragraph iframe{
    margin-top: 2rem;
}


@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__videobox{
        width: 90%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
        /* background-color: aqua; */
        margin-top: 2rem;
        
        
    }

    .app__videobox-action{
        padding-top: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
       }

    .app__videobox-img{
        width: 100%;
    }

    .app__videobox-content{

        align-items: center;
        justify-content: center;
        width: 80%;
        padding-left: 0;
        /* background-color: blueviolet; */
    }

    .app__videobox-head{

        align-items: center;
        justify-content: center;
        text-align: center;
        /* background-color: blue; */
    }

    .app__videobox-paragraph {

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }

    .app__videobox-paragraph iframe{
        margin-top: 2rem;
    }

    .app__videobox-tagline{

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }


}

