.app__footer {
    width: 100%;
    /* position: relative; */
    /* z-index: 1; */
    height: fit-content;
    padding:1rem 0;

    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    /* background-color: blue; */

    background: var(--color-orange);
    /* padding-top: 0; */
    margin: 0;
    margin-top: 0rem;
    /* background-color: aquamarine; */
}

.app__footer-links {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    height: auto;
    margin-top: 0rem;
    /* padding-top: 2rem; */
    /* background-color: aqua; */
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work {
    flex: 1;
    /* margin: 1rem; */
    width: 80%;
    text-align: center;
    /* background-color: blanchedalmond; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.app__footer-links_logo img:first-child {
    width: 190px;
    /* margin-bottom: 0.75rem; */
    /* background-color: #ccc; */
}


.app__footer-links_work p:nth-child(2n + 1) {
    /* margin-bottom: 1rem; */
}

.app__footer-links_icons {
    /* margin-top: 0.5rem; */
    display: flex;
    justify-content: center;
    /* background-color: bisque; */

}

.app__footerinstagram {
width: 65px;
height: 65px;
display: flex;
    justify-content: center;
    align-items: center;

/* background-color: aqua; */
}

.app__footerinstagram img{
    /* width: 90%; */
    height: 50%;
}

.app__footer-links_icons svg {
    color: var(--color-white);
    /* margin: 0.5rem; */
    font-size: 24px;
    cursor: pointer;
}

.app__footer-links_icons svg:hover {
    /* color: #ccc; */
}

.app__footer-headtext {
    font-family: var(--font-base);
    color: var(--color-white);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-size: 25px;
    line-height: 30.6px;
    margin-bottom: 1rem;
    text-align: center;
    /* background-color: aqua; */
}

.footer__copyright {
    margin-top: 0.5rem;
    text-align: center;
    /* background-color: violet; */
}


.text-color__blue{
    color: #0000FF;
}

@media screen and (min-width: 2000px) {
    .app__footer-headtext {
        font-size: 51px;
        /* line-height: 61.6px; */
    }
}

@media screen and (max-width: 1150px) {
    .app__footer-links {
        align-items: center;
        flex-direction: column;
        padding: 0;
    }

    .app__footer-links_contact,
    .app__footer-links_logo,
    .app__footer-links_work {
        /* margin: 1rem 0; */
        width: 80%;
        /* background-color: blueviolet; */
    }
}

@media screen and (max-width: 650px) {
    .app__footer {
        padding: 0 0 1rem 0;
    }

    .app__footerinstagram img{
        /* width: 90%; */
        height: 40%;
    }
}

@media screen and (max-width: 350px) {
    .app__footer-links_logo img:first-child {
        width: 80%;
    }
}