
.app__listbox {
    width: 80%;
    margin: auto;
    

    display: flex;
    flex-direction: row;
    /* height: 300px; */
    /* background-color: mediumaquamarine; */
    justify-content:flex-start;
}

.app__listbox-img{
    width: 40%;
    /* height: 100%; */
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 100%; */
    

}

.app__listbox-img img{
    width: 100%;
    height: 90%;
    object-fit: fill;

}

.app__listbox-content{
    width: 60%;

    padding: 1rem;
}

.app__listbox-action{
 padding-top: 1rem;
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 font-style: italic;
 /* background-color: aquamarine; */
}

.app__listbox__icon{
    /* padding: 1rem; */
    color:var(--color-black);
    font-size: 24px;
    padding: 0.5rem ;
    padding-left: 1.5rem;
    padding-top: 0.6rem;
    /* background-color: aqua; */

}

.app__listbox-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}



@media screen and (min-width: 2000px) {

}

@media screen and (max-width: 1150px) {

    .app__listbox{
        width: 90%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
        /* background-color: aqua; */
        margin-top: 4rem;
        
        
    }

    .app__listbox-content{
        width: 100%;
    
        /* padding: 1rem; */
    }

    .app__listbox-action{
        padding-top: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
       }

    .app__listbox-img{
        width: 100%;
    }

    .app__listbox-content{

        align-items: center;
        justify-content: center;
        /* background-color: blueviolet; */
    }

    .app__listbox-head{

        align-items: center;
        justify-content: center;
        text-align: center;
        /* background-color: blue; */
    }

    .app__listbox-paragraph{

        align-items: center;
        justify-content: center;
        /* background-color: red; */
        text-align: center;

    }



 

}

