
.app__attire {
    width: 90%;
    margin: auto;
    

    display: flex;
    flex-direction: row;
    /* height: 500px; */
    /* background-color: aquamarine; */
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.app__attire-img{
    width: 40%;
    /* height: 100%; */
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    
    

}

.app__attire-img img{
    width: 100%;
    
    /* object-fit: cover; */

}

.app__attire-content{
    width: 50%;

    /* padding: 1rem; */
    padding-left: 2rem;
    /* background-color: aqua; */
}

.app__attire-action{
 padding-top: 1rem;
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 font-style: italic;
}

.app__attire__icon{
    /* padding: 1rem; */
    color:var(--color-black);
    font-size: 24px;
    padding: 0.5rem ;
    padding-left: 1.5rem;
    padding-top: 0.6rem;
    /* background-color: aqua; */

}

.app__attire-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__archivesdetails-videoholder{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* height: 90; */
    /* margin: auto; */
    /* background-color: blue; */
    grid-column: 1/ span 3;
    /* padding: 1rem; */
    margin-top: 5rem;

    padding-bottom: 2rem;

}





@media screen and (max-width: 850px) {
    .app__archivesdetails-videoholder{
        margin-top: 0;
    }
    
}

@media screen and (max-width: 1150px) {
    .app__attire{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .app__attire-img{
        width: 80%;
    }

    .app__attire-content{
        width: 80%;
        margin-top: 2rem;
    }

    .app__archivesdetails-videoholder{
        margin-top: 2rem;
    }
    
    
}

@media screen and (max-width: 450px) {

}